#index-page{
    @extend %bg-position;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    background-image: url("../assets/home/desktop.jpg");
    @media #{$media-lg} {
        justify-content: start;
        background-image: url("../assets/home/tablet.jpg");
    }
    @media #{$media-md} {
        background-image: url("../assets/home/mobile.jpg");
    }

    .main-page{
        color: $white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 8rem 3.5rem;
        @media #{$media-lg} {
            flex-direction: column;
            justify-content: space-around;
            padding: 4rem;
            flex: 1;
        }
        @media #{$media-sm} {
            padding: 0 0.5rem 1rem;
        }
        .intro{
            width: 40%;
            @media #{$media-lg} {
                width: 80%;
                text-align: center;
            }
            .title{
                @extend .heading5;
                letter-spacing: 4px;
                font-weight: 100;
                opacity: 0.6;
            }
            .text{
                letter-spacing: 2px;
                font-weight: 100;
                opacity: 0.6;
                text-align: justify;
                line-height: 1.5;
                @media #{$media-lg} {
                    text-align: center;
                }
            }
        }
        .explore-container{
            width: 30%;
            @media #{$media-sm} {
                width: 100%;
            }
            .explore{
                margin: auto;
                font-size: 40px;
                font-family: 'Bellefair';
                color: $black;
                background-color: $white;
                border-radius: 50%;
                width: var(--explore-width);
                height: var(--explore-width);
                text-align: center;
                text-transform: uppercase;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                z-index: 1;
                cursor: pointer;
                @media #{$media-md} {
                    font-size: 29px;
                }
                span{
                    z-index: 2;
                    display: block;
                    width: fit-content;
                }
                &:hover::after{
                    width: calc(var(--explore-width) + 0.5 * var(--explore-width));
                    height: calc(var(--explore-width) + 0.5 * var(--explore-width));
                }
                &::after{
                    transition: 1s;
                    content: '';
                    width: calc(var(--explore-width));
                    height: calc(var(--explore-width));
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    background-color: rgba($white,0.2);
                    border-radius: 50%;
                }
            }
        }
    }
}