@import './libs/functions.scss';
@import './libs/mixins.scss';
@import './libs/variables.scss';


@font-face {
    font-family: 'Barlow';
    src: url("../fonts/BarlowCondensed.ttf");
}

@font-face {
    font-family: 'Bellefair';
    src: url("../fonts/Bellefair.ttf");
}


*{
    box-sizing: border-box;
}

body{
    margin: 0;
    width: 100vw;
    height: 100vh;
    font-size: 18px;
    font-family: 'Barlow';
}
#react-page{
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.heading1{
    text-transform:uppercase;
    letter-spacing:3px;
    font-size: 150px;
    font-family: 'Bellefair';
    margin: 2rem 0;
    font-weight:400;
    @media #{$media-sm} {
        font-size: calc(150px / 1.5);
    }
}
.heading2{
    text-transform:uppercase;
    letter-spacing:3px;
    font-size: 100px;
    font-family: 'Bellefair';
    margin: 1.5rem 0;
    font-weight:400;
    @media #{$media-sm} {
        font-size: calc(100px / 1.5);
    }
}
.heading3{
    text-transform:uppercase;
    letter-spacing:3px;
    font-size: 56px;
    font-family: 'Bellefair';
    margin: 1.5rem 0;
    font-weight:400;
    @media #{$media-sm} {
        font-size: calc(56px / 1.5);
    }
}
.heading4{
    text-transform:uppercase;
    letter-spacing:3px;
    font-size: 32px;
    font-family: 'Bellefair';
    margin: 1rem 0;
    font-weight:400;
    @media #{$media-sm} {
        font-size: calc(32px / 1.5);
    }
}
.heading5{
    text-transform:uppercase;
    letter-spacing:3px;
    font-size: 28px;
    font-family: 'Barlow';
    margin: 1rem 0;
    @media #{$media-sm} {
        font-size: calc(28px / 1.5);
    }
}
.subheading1{
    text-transform:uppercase;
    letter-spacing:3px;
    font-size: 28px;
    font-family: 'Bellefair';
    margin: 0.5rem 0;
    @media #{$media-sm} {
        font-size: calc(28px / 1.5);
    }
}
.subheading2{
    text-transform:uppercase;
    letter-spacing:3px;
    font-size: 14px;
    font-family: 'Barlow';
    margin: 0.5rem 0;
    @media #{$media-sm} {
        font-size: calc(14px / 1.5);
    }
}
.navtext{
    font-size: 16px;
    font-family: 'Barlow';
    text-transform: uppercase;
    cursor: pointer;
    color: $white;
    letter-spacing: 2px;
    margin: 0 0.75rem;
    transition: 0.3s ease-out;
    border: 3px solid transparent;
    &.active{
        transition: 1s;
        border-bottom: $white solid 3px;
    }
    &:not(.active):hover{
        border-bottom: rgba($white,0.5) solid 3px;
    }

}

%bg-position{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

@keyframes menu-after {
    0%{
        opacity: 0;
        transform: translateY(0) rotateZ(0) scale(1);
    }
    50%{
        opacity: 0;
    }
    100%{
        opacity: 1;
        transform: translateY(50%) rotateZ(-45deg) scale(0.8);
    }
}

.header{
    flex: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: calc(96px + 40px);
    width: 100%;
    padding-top: 40px;
    position: relative;
    @media #{$media-sm} {
        height: calc(96px + 20px);
        padding-top: 20px;
    }
    .logo{
        width: calc(46px + 70px);
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        @media #{$media-sm} {
            width: 100%;
            justify-content: space-between;
            padding: 0 2rem;
            z-index: 150;
        }

        .menu-btn{
            display: none;
            @media #{$media-md} {
                position: relative;
                display: flex;
                flex: none;
                justify-content: center;
                align-items: center;
                width: 48px;
                height: 36px;

                span{
                    transition: 0.5s;
                    height: 12%;
                    width: 100%;
                    background-color: white;
                    border-radius: 7px;
                }
                &::after,&::before{
                    transition: 1s;
                    position: absolute;
                    content: '';
                    left:0;
                    height: 12%;
                    width: 100%;
                    background-color: white;
                    border-radius: 7px;
                }
                &::after{
                    top: 0;
                }
                &::before{
                    bottom: 0;
                }
                &.active{
                    span{
                        opacity: 0;
                    }
                    &::after{
                        transform-origin: right center;
                        animation: menu-after 1s both;
                    }
                    &::before{
                        transform-origin: right center;
                        transform: translateY(-50%) rotateZ(45deg) scale(0.8);
                    }
                }
            }
        }
    }
    nav{
        width: 60%;
        display: flex;
        align-items: center;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.055);
        backdrop-filter: blur(25px);
        padding-left: 10%;
        position: relative;
        z-index: 100;
        @media #{$media-lg} {
            width: auto;
        }
        @media #{$media-sm} {
            position: fixed;
            animation: slide-in 1s both;
            top:0;
            right: 0;
            height: 100vh;
            width: 260px;
            flex-direction: column;
            padding: calc(116px + 4rem) 0 0 10px;
            &.hide{
                display: none;
            }
            &.hiding{
                animation: slide-out-right 1s both;
            }
        }
        
        .link{
            @extend .navtext;
            display: flex;
            align-items: center;
            height: 100%;
            @media #{$media-sm} {
                height: auto;
                display: block;
                width: 100%;
                padding: 1rem;
                letter-spacing: 5px;
                &.active{
                    border-bottom-color: transparent;
                    border-right-color: $white;
                }
            }
            .number{
                letter-spacing: 4px;
                display: inline-block;
                margin: 0 0.5rem;
                font-weight: 700;
            }
            .text{
                font-weight: 100;
                opacity: 0.8;
            }
        }
        &::before{
            content: '';
            width: calc((400% / 6) - 110px - 46px + 5%);
            height: 2px;
            opacity: 0.2;
            background-color: $white;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: calc((-400% / 6) + 110px + 46px);
            @media #{$media-lg} {
                display: none;
            }
        }
    }
}

@keyframes appear {
    0%{
        filter: blur(10px);
        opacity: 0.7;
    }
    50%{
        opacity: 1;
    }
    100%{
        filter: blur(0);
    }
}
@keyframes hide {
    0%{
        filter: blur(0);
        opacity: 1;
    }
    50%{
        opacity: 0.7;
    }
    100%{
        filter: blur(10px);
    }
}

.page-content{
    width: 100%;
    height: 100%;
    overflow-y: auto;
    &.appearing{
        animation: appear 0.7s both;
    }

    &.hiding{
        animation: hide 0.7s both;
    }
}

@import "./templates/index.scss";
@import "./templates/destination.scss";
@import "./templates/crew.scss";
@import "./templates/technology.scss";