@keyframes blur-in {
    0%{
        filter: blur(20px);
        opacity: 0;
    }
    100%{
        filter: blur(0);
        opacity: 1;
    }
}
@keyframes blur-out {
    0%{
        filter: blur(0);
        opacity: 1;
    }
    100%{
        filter: blur(20px);
        opacity: 0;
    }
}
#crew-page{
    @extend %bg-position;
    background-image: url("../assets/crew/desktop.jpg");
    @media #{$media-lg} {
        background-image: url("../assets/crew/tablet.jpg");
    }
    @media #{$media-md} {
        background-image: url("../assets/crew/mobile.jpg");
    }
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    @media #{$media-lg} {
        justify-content: flex-start;
    }
    .main-page{
        padding-left: 7rem;
        @media #{$media-xl} {
            padding-left: 2rem;
        }
        @media #{$media-lg} {
            padding: 2rem 2rem 0;
            flex: 1;
            display: flex;
            flex-direction: column;
        }
        .heading5{
            flex: none;
            color: $white;
            letter-spacing: 6px;
            font-weight: 200;
            .number{
                opacity: 0.5;
                font-weight: 700;
                display: inline-block;
                margin-right: 0.75rem;
            }
        }
        .main-content{
            flex: 1;
            margin-top: 2rem;
            display: flex;
            align-items: center;
            @media #{$media-lg} {
                margin-top: 0;
                flex-direction: column;
            }
            @media #{$media-md} {
                flex-direction: column-reverse;
            }
            .infos{
                width: 50%;
                z-index: 10;
                @media #{$media-lg} {
                    flex: none;
                    width: 100%;
                    text-align: center;
                }
                @media #{$media-md} {
                    display: flex;
                    flex-direction: column-reverse;
                    margin-top: 1rem;
                }
                nav{
                    z-index: 15;
                    display: flex;
                    @media #{$media-lg} {
                        justify-content: center;
                        margin: 0.5rem 0;
                    }
                    .link{
                        display: block;
                        height: 10px;
                        width: 10px;
                        margin: 0 0.75rem;
                        border-radius: 50%;
                        background-color: rgba(darken($white,15%),0.5);
                        &.active{
                            background-color: $white
                        }
                        &:not(.active):hover{
                            background-color: rgba($color: $white, $alpha: 0.8);
                        }
                        @extend .navtext;
                    }
                }
                .specific-infos{
                    position: relative;
                    min-height: 50vh;
                    color: $white;
                    @media #{$media-lg} {
                        min-height: 40vh;
                    }
                    .tab{
                        display: block;
                        position: absolute;
                        top: 50%;
                        left: 0;
                        transform: translateY(-50%);
                        width: 100%;
                        height: fit-content;
                        animation: fade-in 1s both;
                        letter-spacing: 5px;
                        @media #{$media-md} {
                            top: 0;
                            transform: translateY(0);
                        }
                        .heading4{
                            font-weight: 100;
                            opacity: 0.5;
                            margin: 0.5rem 0;
                        }
                        .heading3{
                            font-weight: 100;
                            margin: 0.75rem;
                        }
                        .description{
                            opacity: 0.7;
                            margin: 1.5rem 0;
                            line-height: 1.8;
                            letter-spacing: 2px;
                        }
                        &.hide{
                            display: none;
                        }
                        &.hiding{
                            animation: fade-out 1s both;
                        }
                    }
                }
            }
            .crew-container{
                flex: none;
                width: 55%;
                min-height: 65vh;
                position: relative;
                @media #{$media-lg} {
                    width: 100%;
                    min-height: 35vh;
                    flex: 1;
                }
                @media #{$media-md} {
                    margin-bottom: 1rem;
                    min-height: 50vh;
                }
                .crew{
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 115%;
                    display: block;
                    animation: blur-in 1s both;
                    @media #{$media-lg} {
                        height: 95%;
                    }
                    img{
                        height: 100%;
                        max-width: 100%;
                        width: auto;
                        margin: auto;
                        display: block;
                    }
                    &.hide{
                        display: none;
                    }
                    &.hiding{
                        animation: blur-out 1s both;
                    }
                }
            }
        }
    }
}