@keyframes slide-in {
    0%{
        transform: translateX(30vw);
        opacity: 0;
    }
    100%{
        opacity: 1;
        transform: translateX(0);
    }
}
@keyframes slide-out {
    0%{
        transform: translateX(0);
        opacity: 1;
    }
    100%{
        transform: translateX(-30vw);
        opacity: 0;
    }
}
@keyframes slide-out-right {
    0%{
        transform: translateX(0);
        opacity: 1;
    }
    100%{
        transform: translateX(30vw);
        opacity: 0;
    }
}
@keyframes fade-out {
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
@keyframes fade-in {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
#destination-page{
    @extend %bg-position;
    background-image: url("../assets/destination/desktop.jpg");
    @media #{$media-lg} {
        background-image: url("../assets/destination/tablet.jpg");
    }
    @media #{$media-md} {
        background-image: url("../assets/destination/mobile.jpg");
    }
    .main-page{
        padding: 3rem 7rem;
        @media #{$media-lg} {
            padding: 3rem 6rem;
        }
        @media #{$media-md} {
            padding: 2rem;
        }
        .heading5{
            color: $white;
            letter-spacing: 6px;
            font-weight: 200;
            .number{
                opacity: 0.5;
                font-weight: 700;
                display: inline-block;
                margin-right: 0.75rem;
            }
        }
        .main-content{
            margin-top: 4rem;
            display: flex;
            align-items: center;
            @media #{$media-lg} {
                flex-direction: column;
            }
            .planet-container{
                flex: none;
                width: 55%;
                min-height: 50vh;
                position: relative;
                overflow-x: hidden;
                @media #{$media-lg} {
                    width: 100%;
                    min-height: 35vh;
                }
                .planet{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    display: block;
                    animation: slide-in 1s both;
                    img{
                        height: 90%;
                        width: auto;
                        margin: auto;
                        display: block;
                    }
                    &.hide{
                        display: none;
                    }
                    &.hiding{
                        animation: slide-out 1s both;
                    }
                }
            }
            .infos{
                flex: 1;
                @media #{$media-lg} {
                    flex: none;
                    width: 100%;
                    text-align: center;
                }
                nav{
                    display: flex;
                    @media #{$media-lg} {
                        justify-content: center;
                        margin: 0.5rem 0;
                    }
                    .link{
                        line-height: 2.5;
                        letter-spacing: 4px;
                        margin: 0 0.75rem;
                        color: rgba($color: $white, $alpha: 0.5);
                        &.active{
                            color: $white
                        }
                        &:not(.active):hover{
                            color: rgba($color: $white, $alpha: 0.8);
                        }
                        @extend .navtext;
                    }
                }
                .specific-infos{
                    position: relative;
                    min-height: 50vh;
                    color: $white;
                    @media #{$media-lg} {
                        min-height: 25vh;
                    }
                    .tab{
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        animation: fade-in 1s both;
                        .heading2{
                            letter-spacing: 5px;
                        }
                        .description{
                            font-weight: 100;
                            opacity: 0.7;
                            margin-bottom: 3rem;
                            line-height: 1.8;
                            letter-spacing: 2px;
                        }
                        &.hide{
                            display: none;
                        }
                        &.hiding{
                            animation: fade-out 1s both;
                        }
                        .additional-infos{
                            padding: 2rem 0;
                            border-top: 2px solid rgba($white,0.2);
                            display: flex;
                            @media #{$media-lg} {
                                justify-content: space-around;
                            }
                            @media #{$media-sm} {
                                flex-direction: column;
                            }
                            .subheading2{
                                opacity: 0.5;
                            }
                            &>*{
                                margin-right: 2.5rem;
                            }
                        }
                    }
                }
            }
        }
    }
}