#technology-page{
    @extend %bg-position;
    background-image: url("../assets/technology/desktop.jpg");
    @media #{$media-lg} {
        background-image: url("../assets/technology/tablet.jpg");
    }
    @media #{$media-md} {
        background-image: url("../assets/technology/mobile.jpg");
    }
    .main-page{
        padding: 2rem 0;
        padding-left: 7rem;
        @media #{$media-xl} {
            padding-left: 2rem;
        }
        @media #{$media-lg} {
            padding: 2rem 2rem 0;
            flex: 1;
            display: flex;
            flex-direction: column;
        }
        .heading5{
            flex: none;
            color: $white;
            letter-spacing: 6px;
            font-weight: 200;
            .number{
                opacity: 0.5;
                font-weight: 700;
                display: inline-block;
                margin-right: 0.75rem;
            }
        }
        .main-content{
            flex: 1;
            margin-top: 2rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media #{$media-lg} {
                flex-direction: column-reverse;
                margin: 0 -2rem;
            }
            .infos{
                width: 55%;
                z-index: 10;
                display: flex;
                @media #{$media-lg} {
                    flex: none;
                    width: 100%;
                    text-align: center;
                    display: block;
                    padding: 0 3rem;
                }
                @media #{$media-lg} {
                    padding: 0 1rem;
                }
                nav{
                    z-index: 15;
                    display: flex;
                    justify-content: center;
                    flex-direction: column;
                    margin-right: 5rem;
                    @media #{$media-lg} {
                        flex-direction: row;
                        justify-content: center;
                        margin: 2rem 0;
                    }
                    .link{
                        height: 70px;
                        width: 70px;
                        line-height: 70px;
                        text-align: center;
                        font-size: 30px;
                        margin: 1rem 0;
                        border-radius: 50%;
                        border: 1px solid rgba($white,0.5);
                        font-family: 'Bellefair';
                        @media #{$media-lg} {
                            flex-direction: row;
                            justify-content: center;
                            margin: 0 1rem;
                        }
                        @media #{$media-sm} {
                            height: 60px;
                            width: 60px;
                            line-height: 60px;
                            font-size: 30px;
                        }
                        &.active{
                            color: $black;
                            background-color: $white;
                        }
                        &:not(.active):hover{
                            background-color: rgba($color: $white, $alpha: 0.8);
                            color: $black;
                        }
                        @extend .navtext;
                    }
                }
                .specific-infos{
                    position: relative;
                    min-height: 50vh;
                    flex: 1;
                    color: $white;
                    @media #{$media-lg} {
                        min-height: 35vh;
                    }
                    .tab{
                        display: block;
                        position: absolute;
                        top: 50%;
                        left: 0;
                        transform: translateY(-50%);
                        width: 100%;
                        height: fit-content;
                        animation: fade-in 1s both;
                        letter-spacing: 5px;
                        @media #{$media-lg} {
                            top: 0;
                            transform: translateY(0);
                        }
                        .heading5{
                            font-weight: 100;
                            opacity: 0.5;
                            margin: 0.5rem 0;
                        }
                        .heading3{
                            font-weight: 100;
                            margin: 0.75rem;
                        }
                        .description{
                            opacity: 0.7;
                            margin: 1.5rem 0;
                            line-height: 1.8;
                            letter-spacing: 2px;
                        }
                        &.hide{
                            display: none;
                        }
                        &.hiding{
                            animation: fade-out 1s both;
                        }
                    }
                }
            }
            .tech-container{
                flex: none;
                width: 40%;
                min-height: 65vh;
                position: relative;
                @media #{$media-lg} {
                    width: 100%;
                    min-height: calc(380 * 100vw / 820);
                    flex: 1;
                }
                .tech{
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 90%;
                    display: block;
                    animation: blur-in 1s both;
                    @media #{$media-lg} {
                        height: 95%;
                    }
                    img{
                        height: 100%;
                        max-width: 100%;
                        margin-left: auto;
                        width: auto;
                        display: block;
                        &.landscape{
                            display: none;
                        }
                        @media #{$media-lg} {
                            &.landscape{
                                display: block;
                                width: 100%;
                                margin: 1rem 0;
                            }
                            &.portrait{
                                display: none;
                            }
                        }
                    }
                    &.hide{
                        display: none;
                    }
                    &.hiding{
                        animation: blur-out 1s both;
                    }
                }
            }
        }
    }
}