// Colors
$black:#0b0d17;
$blue:#d0d6f9;
$white:#ffffff;

$danger:rgb(231, 58, 58);
$success:rgb(14, 184, 23);
$warning:rgb(243, 221, 20);

// Color text - without the #, used for the icons
$black-text: str_replace('#{$black}','#','');
$blue-text: str_replace('#{$blue}','#','');
$white-text: str_replace('#{$white}','#','');

// Media sizing
$size-xl: 1200px;
$size-lg: 992px;
$size-md: 768px;
$size-sm: 576px;
$media-xl: "(max-width: #{$size-xl})";
$media-lg: "(max-width: #{$size-lg})";
$media-md: "(max-width: #{$size-md})";
$media-sm: "(max-width: #{$size-sm})";

*{
    --explore-width:274px;
    @media #{$media-lg} {
        --explore-width:242px;        
    }
    @media #{$media-md} {
        --explore-width:200px;        
    }
}